export default {
    data() {
            return {
                step: 2,
                name: '',
                id: '',
            }
        },
    methods: {
        auth() {
            this.$http({
                method: 'post',
                url: `${BASE_URL}/lock-account/verify`,
                headers: {
                    'Content-Type': 'application/json',
                    'X-Auth-Token': window.USER.token
                },
                data: {
                    idcard: this.id,
                    name: this.name
                }
            }).then(function(result) {
                const {
                    status,
                } = result.data;
                if (status == 0) {
                    this.$vux.toast.show({
                        text: '成功'
                    })
                    window.USER.isRealNameAuth = true;
                    localStorage.setItem(USER_CACHE_KEY, JSON.stringify(window.USER));
                    this.$router.replace({
                        path: '/'
                    });
                } else {
                    this.$vux.toast.show({
                        type: 'warn',
                        text: '错误：' + status
                    })
                }
            }.bind(this)).catch(function(e) {
                this.$vux.toast.show({
                    type: 'warn',
                    text: '出现错误'
                })
                console.log(e);
            }.bind(this));
        }
    },
    computed: {
        authDisabled: function () {
            if (this.name && this.id) {
                return false;
            } else {
                return true;
            }
        }
    },
    created() {}
}

function gen_signature() {
    var utc = new Date().getTime();
    var str = Math.random().toString(36).slice(2);
    return utc + '-' + str;
}



